body{
  background-color: #9266ff;
}

.App{
  background-color: #3f3160;
  min-height: 500px;
  width: 400px;
  margin: 40px auto;
}

.H1_cls{
  margin: 20px;
  padding: 10px 0 0 0;
  color: #fff;
  font-family: Verdana;
  text-align: center;
}

#to-do-form input{
  background-color: rgb(100, 150, 190);
  border: 0;
  width: 240px;
  height: 50px;
  padding: 0 10px;
  border-radius: 5px;
  margin: 10px;
  font-size: 18px;
  border-radius: 5px;
  color: #fff;
}

#to-do-form input::placeholder{
  color: #efefef;
}

#to-do-form button{
  height: 50px;
  width:80px;
  padding: 20px auto;
  border-radius: 40px;
  border: 0;
  background-color: rgb(202,156,24);
  color: white;
  cursor: pointer;
  font-weight: bolder;
  font-size: 20px;
}