.list{
    color:#fff;
    width: 380px;
    background-color: #07bf3e;
    height:50px;
    margin: 20px auto;
}
.list p{
    margin: 20px auto;
    padding: 14px;
    position: relative;
}

.list p span{
    position: absolute;
    right:10px;
    cursor: pointer;
}

.list p input{
    background-color: transparent;
    border: 0;
    color: #fff;
    font-size: 18px;
    outline: none;
}

.list p button{
    height: 24px;
    width: 24px;
    border-radius: 40px;
    border: 0;
    margin: -10px auto;
}